<template>
  <div id="past" class="tab-pane tab-box">
    <div v-infinite-scroll="loadMore" infinite-scroll-disabled="busy" infinite-scroll-distance="10">
    <div class="orders-lst" v-if="pastorders.length > 0">
      <router-link
        v-for="(past,index) in pastorders"
        :key="index"
        :to="{ name: 'order-details',params: { order_id: past.id }}"
      >
        <div class="order-item d-flex align-items-start">
          <img :src="past.icon_image" />
          <div class="order-data ml-3 d-flex align-items-start justify-content-between w-100">
            <div class="orders-status-main delivery-link">
              <h6 class="mb-1">{{past.restaurant_name}}</h6>
              <span v-html="currencys"></span> <span>{{ parseFloat(past.total_amount).toFixed(2)}}</span>
              <p class="mt-2">{{moment.utc(past.order_date).fromNow()}}</p>
            </div>
            <div class="order-status" v-if="past.order_status == 'Delivered'">
              <p class="delivered order-status-btn">{{past.order_status}}</p>
            </div>
            <div class="order-status" v-else>
              <p class="canceled order-status-btn">{{past.order_status}}</p>
            </div>
          </div>
        </div>
      </router-link>
    </div>
    <div v-else class="order-offers">
      <div class="text-center my-2" v-if="loading">
        <b-spinner class="align-middle"></b-spinner>
      </div>
      <p v-else>No Past Order Found</p>
    </div>
    </div>
  </div>
</template>
<script>
import { mapActions, mapGetters, mapState } from "vuex";
import moment from "moment";
export default {
  computed: {
    ...mapGetters("order",["pastOrder"]),
     ...mapGetters("setting",["settings"]),
    ...mapState({
      currency: (state) => state.setting.settings.currency,
    }),
  },
  data() {
    return {
      loading: true,
      pastorders: [],
      perPage: 10,
      moment: moment,
      currencys: '',
      // busy: false
    };
  },
  mounted() {
    this.getPastOrders();
    this.currencys = this.currency
  },
  methods: {
    ...mapActions("order",["getPastOrder"]),
    getPastOrders() {
      this.loading = true;
      this.getPastOrder({
        vendor_id: window.location.pathname.split('/')[2],
        is_langauge : "en",
        user_id: "",
        totalrecord: "",
        skip: "",
      }).then((data) => {
        if (data.code == 200) {
          this.loading = false;
          this.pastorders = data.Result;
        }
      });
    },
    loadMore: function() {
      this.loading = true;
      
      setTimeout(() => {
        // this.pastorders.push(...this.pastorders);
        this.loading = false;
      }, 1000);
    }
  },
};
</script>